    methods: {
        async submitAllApplications() {
            try {
                // Send all applications in the submissions array to the Flask server
                const response = await axios.post('http://localhost:5005/add_applications', {
                    applications: this.submissions,  // Send the array to the server
                });

                // Show success message
                this.message = response.data.message;
                this.submissions = []; // Clear the submissions array after success
                console.log(`${this.message} maa niqqa...`);

            } catch (error) {
                // Handle errors
                this.message = 'Error: ' + error.response?.data?.error || error.message;
                console.log(`${this.message} maa niqqa...`);
            }
        },
        formatNumber(num) {
            return Number(num).toLocaleString('en-US');
        },
        formatDate(timestamp) {
            if (!timestamp || !timestamp.toDate) return "";
            const jsDate = timestamp.toDate();
            return jsDate.toLocaleDateString(undefined, {
                year: "numeric",
                month: "long",
                day: "numeric",
                hour: "2-digit",
                minute: "2-digit",
                second: "2-digit",
            });
        },
        formattedParentGroup(g) {
            let _group = g;
            Object.keys(this.parentGroups).forEach(key => {
                const group = this.parentGroups[key];
                if (_group === group.name) {
                    _group = group.label;
                }
                if (_group === null) {
                    _group = 'Hakuchagua kundi';
                }
            });
            return _group;
        },
        formattedGroup(g) {
            let _group = g;
            Object.keys(this.groups).forEach(key => {
                const group = this.groups[key];
                if (_group === group.name) {
                    _group = group.label;
                }
                if (_group === null) {
                    _group = '';
                }
            });
            return _group;
        },
        fetchSubmissions() {
            try {
                if (this.unsubscribe) this.unsubscribe();

                const q = query(
                    collection(db, "applications"),
                    orderBy("regDate", "desc")
                );

                this.unsubscribe = onSnapshot(q, async (querySnapshot) => {
                    this.submissions = querySnapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
                    this.totalSubmissions = querySnapshot.size;
                    // await this.submitAllApplications();
                });
            } catch (error) {
                console.error("Error fetching submissions:", error);
            }
        },
        handlePageChange(page) {
            this.currentPage = page;
            this.fetchSubmissions();
        },
        async handleDelete(id) {
            try {
                await deleteDoc(doc(db, "applications", id));
                this.submissions = this.submissions.filter((submission) => submission.id !== id);
                this.totalSubmissions -= 1;
                console.log("Submission deleted successfully!");
            } catch (error) {
                console.error("Error deleting submission:", error);
            }
        },
    },
    computed: {
        indexedSubmissions() {
            return this.submissions.map((item, index) => ({
                ...item,
                index: index + 1
            }));
        }
    },
    mounted() {
        this.fetchSubmissions();
    },
    beforeUnmount() {
        if (this.unsubscribe) this.unsubscribe();
