import { createApp } from 'vue';

// Vuetify setup
import 'vuetify/styles'
import { createVuetify } from 'vuetify'
import * as components from 'vuetify/components'
import * as directives from 'vuetify/directives'

// Ant Design Vue setup
import Antd from 'ant-design-vue';
import 'ant-design-vue/dist/reset.css';  // Make sure to include this before using Antd
import '@mdi/font/css/materialdesignicons.css';
// Other styles
//import 'metro-ui/build/css/metro.css';
import 'w3-css/w3.css';
import './assets/custom.css'; // Custom CSS
import "@fontsource/gowun-batang";

// Components
import App from './App.vue'

// Create Vuetify instance
const vuetify = createVuetify({
    icons: {
        iconfont: 'mdi', // Set the icon set to use (Material Design Icons in this case)
      },
  components,
  directives,
});



const app = createApp(App);

// Use Vuetify first to avoid conflicts, then Ant Design Vue
app.use(vuetify);  // Use Vuetify
app.use(Antd);     // Use Ant Design Vue
app.mount('#app');  // Mount the app
